.traditions-section {
  margin-top: 0;
  padding-top: 40px;

  @media screen and (min-width: $bp-desktop) {
    padding-top: 96px;
  }

  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 40px;
  }

  &__content {
    max-width: 710px;
  }

  &__title {
    margin-bottom: 24px;

    @media screen and (min-width: $bp-desktop) {
      margin-bottom: 42px;
    }
  }

  &__text {
    &--limited-first {
      max-width: 350px;
      margin-bottom: 16px;

      @media screen and (min-width: $bp-desktop) {
        max-width: 700px;
        margin-bottom: 42px;
      }
    }

    &--limited-second {
      max-width: 350px;
      margin-bottom: 0px;
    }
  }

  &__bakerman-photo {
    display: none;

    @media screen and (min-width: $bp-tablet) {
      display: block;
      border-radius: 400px 400px 0px 0px;
      width: 300px;
    }

    @media screen and (min-width: $bp-desktop) {
      width: 393px;
    }
  }
}
